ds—along with my imagination—to literary use was daunting, therefore, always putting it off. That is, until 2011 when I finally took the plunge and picked up that pen and notebook (still have them, too). I began to string those words into sentences, then paragraphs, then pages, eventually joining them together to create my first novel—The Sixth Amulet - Book One. Then my imagination had other plans; it didn’t want to stop at just one. Why would it? And so, one novel turned into two, then three, then … Who knows where it will end? "),_c('br'),_c('br'),_vm._v(" I believe everyone has a story to tell, a potential book tucked away inside them, dying to get out. And all that’s needed to create it, is a simple recipe: a pen and a notebook. So, open your mind and let your imagination roam through the endless possibilities it has to offer … then simply write! "),_c('br'),_c('br'),_vm._v(" Many people have asked me, “Why Scotland? And why Balloch?” Well, it was while on my first trip there (around 2005), visiting good friends in Balloch, that I fell in love with the place. Always the perfect hosts each time I visit, Billy, Ciara and Megan are so lucky to live a stone’s throw from Balloch Castle, on the stunning shores of Loch Lomond (well worth a visit if you happen to be passing through). Anyway, it just seemed right to set The Sixth Amulet in this location. And who knows? Maybe I’ll retire there … one day. ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('h5',{staticClass:"author_bio_body"},[_vm._v(" It belongs to just one-the Magus-Lord of all Warlocks-and he belongs to it. But the world of mortals has been overshadowed by a menace: an evil entity that would have it in order to dominate and destroy all that is good. "),_c('br'),_vm._v(" And yet, all is not lost. From within this stone, a light is about to re-ignite by innocence, instilling hope into the hearts of those who would risk everything, to see it placed in the hands of the chosen one-to see it shine beyond the darkness. ")])}]

